.content {
  width: calc(100vw - 60px);
  padding-left: 60px;
}

section {
  padding: 16px;
  padding: 96px 16px;
}

/* flex, columns */

.flex {
  display: flex;
}
.flex-center {
  display: flex;
  justify-content: center;
}
.flex-center-hor {
  display: flex;
  align-items: center;
}

.column {
  flex: 1;
  margin: 8px;
}

.framed {
  border: 3px solid blueviolet;
  padding: 40px;
  background-image: linear-gradient(135deg, #fbfbfb 50%, transparent 51%);
  background-size: 100px 100px; /* some initial size to get the slanted appearance */
  background-position: -50px -50px; /* negative positioning to hide it initially */
  background-repeat: no-repeat;
  transition: all ease 0.8s;
}
.framed:hover {
  background-size: 200% 200%; /* 200% because gradient is colored only for 50% */
  background-position: 0px 0px; /* bring it fully into view */
  color: #fff;
}
.colorful {
  padding: 40px;
  background-color: blueviolet;
  color: white !important;
  text-align: center;
  background-image: linear-gradient(135deg, #31302b 50%, transparent 51%);
  background-size: 100px 100px; /* some initial size to get the slanted appearance */
  background-position: -50px -50px; /* negative positioning to hide it initially */
  background-repeat: no-repeat;
  transition: all ease 0.8s;
}
.colorful:hover {
  background-size: 200% 200%; /* 200% because gradient is colored only for 50% */
  background-position: 0px 0px; /* bring it fully into view */
  color: #fff;
}
.colorful img {
  max-width: 80px;
  max-height: 80px;
  object-fit: contain;
}
.colorful h5 {
  margin-bottom: 0;
}
.space {
  height: 200px;
}
.image {
  margin-bottom: 16px;
}
img {
  width: 100%;
}
