.dropcap {
  width: 40px;
  margin-bottom: -8px;
}

.letters img {
  height: 60px;
  object-fit: contain;
  width: 52px;
  margin: 16px 4px;
  &.purple {
    filter: brightness(0) invert(34%) sepia(27%) saturate(6791%)
      hue-rotate(241deg) brightness(99%) contrast(98%);
  }
}

/* arrow */
:root {
  --arrow-length: 4px;
  --arrow-minus: calc(var(--arrow-length) * -1);
  --arrow: calc(var(--arrow-length) * 3);
}
.line2 {
  width: 40px;
  height: 3px;
  background-color: blueviolet;
  float: left;
  transition: ease 150ms;
}
.triangle {
  position: relative;
  float: left;
  margin-left: -8px;
}
.triangle .line2 {
  transform: rotate(45deg);
  width: var(--arrow);
  position: absolute;
  top: -3px;
}
.triangle .bottom {
  transform: rotate(-45deg);
  top: 3px;
}
.framed:hover .main-line {
  width: 180px;
  transition: ease 350ms;
}

/* blog text */
.foot-line {
  height: 3px;
  width: 100%;
  background-color: blueviolet;
  margin-bottom: 24px;
}
.kapitola {
  ul {
    border-top: 2px solid blueviolet;
    padding-left: 24px;
    font-size: 12px;
    font-weight: 400;
    color: blueviolet;
    li p {
      font-size: 12px;
      line-height: 1.5;
      margin: 16px 0;
      margin-left: 8px;
      color: black;
    }
  }
}
